	content: '';
	position: absolute;
	right: 0;
	bottom: 0;
	left: 0;
	height: 50px;
	overflow: hidden;
	box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2),
	            0 8px 0 -3px #f6f6f6,
	            0 9px 1px -3px rgba(0, 0, 0, 0.2),
	            0 16px 0 -6px #f6f6f6,
	            0 17px 2px -6px rgba(0, 0, 0, 0.2);
}

.todo-count {
	float: left;
	text-align: left;
}

.todo-count strong {
	font-weight: 300;
}

.filters {
	margin: 0;
	padding: 0;
	list-style: none;
	position: absolute;
	right: 0;
	left: 0;
}

.filters li {
	display: inline;
}

.filters li a {
	color: inherit;
	margin: 3px;
	padding: 3px 7px;
	text-decoration: none;
	border: 1px solid transparent;
	border-radius: 3px;
}

.filters li a:hover {
	border-color: rgba(175, 47, 47, 0.1);
}

.filters li a.selected {
	border-color: rgba(175, 47, 47, 0.2);
}

.clear-completed,
html .clear-completed:active {
	float: right;
	position: relative;
	line-height: 20px;
	text-decoration: none;
	cursor: pointer;
}

.clear-completed:hover {
	text-decoration: underline;
}

.info {
	margin: 65px auto 0;
	color: #bfbfbf;
	font-size: 10px;
	text-shadow: 0 1px 0 rgba(255, 255, 255, 0.5);
	text-align: center;
}

.info p {
	line-height: 1;
}

.info a {
	color: inherit;
	text-decoration: none;
	font-weight: 400;
}

.info a:hover {
	text-decoration: underline;
}
.vcenter {
    display: flex;
    align-items: center;
    /* justify-content: flex-start */
}
/*
	Hack to remove background from Mobile Safari.
	Can't use it globally since it destroys checkboxes in Firefox
*/
@media screen and (-webkit-min-device-pixel-ratio:0) {
	.toggle-all,
	.todo-list li .toggle {
		background: none;
	}

	.todo-list li .toggle {
		/* height: 40px; */
	}
	.todo-list li label {
		/* height: 40px; */
	}

	.toggle-all {
		-webkit-transform: rotate(90deg);
		transform: rotate(90deg);
		-webkit-appearance: none;
		appearance: none;
	}
}

@media (max-width: 430px) {
	.footer {
		height: 50px;
	}

	.filters {
		bottom: 10px;
	}
}

/* debugging purpose */
/* [class*='row'] {
    border: 2px solid gray;

}

[class*='col-'] {
    border: 2px solid tomato;

} */
.sidebar {
    padding-left: 30px;
    /* background-color: beige; */

}

.sidebar li{
    padding-left:10px;
    padding-top: 5px;
    padding-bottom: 5px;
    
    /* border: 1px solid gray; */
    box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.2),
                0 5px 10px 0 rgba(0, 0, 0, 0.1);
    /* transform: scale(1); */
}
.sidebar li:hover{
    transform: scale(1.1);
}
.sidebar .nav li.active{
/* color: #fff; */
background-color: rgba(219, 213, 212, 0.7);
padding-top: 50px;
/* -webkit-transition: height background-color 500ms ease-in;
-ms-transition: height background-color 500ms ease-in; */
transform: translate(-2px,-1px);
transition: transform 200ms, background-color 200ms, padding 200ms;


}
</style>



